// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import jQuery from 'jquery'
require('bootstrap')
require('jquery.easing')

require('./assets/vendor/bootstrap/css/bootstrap.min.css')
require('./assets/vendor/font-awesome/css/font-awesome.min.css')
require('./assets/vendor/devicons/css/devicons.min.css')
require('./assets/vendor/simple-line-icons/css/simple-line-icons.css')
require('./assets/css/resume.min.css')

Vue.prototype.jQuery = jQuery

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



